.Controles, .Controles div {
    width: 250px;
    display: flex;
    justify-content: center;
}

.Controles button {
    height: 40px;
    border-radius: 40px;
    border: 0;
    flex: 1;
    cursor: pointer;
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 14px;
    letter-spacing: 1px;
    position: relative;
    outline: 0;
    transition: all .3s;
    padding: 0;
    background: white;
}

.Controles button.incio {
    background: cyan;
    color: #fff;
}

.Controles button.incio:hover {
    background: #b64441;
}

.Controles button.reiniciar {
    background: transparent;
    flex: 0;
    margin-right: 25px;
    color: #888;
}

.Controles button.reiniciar:hover {
    color: #666;
}

.Controles button.pausa {
    background: #eea73c;
    color: #fff;
}

.Controles button.pausa:hover {
    background: #d3912f;
}

.Controles button.continuar {
    background: #91b62d;
    color: #fff;
}

.Controles button.continuar:hover {
    background: #7d9e23;
}
