html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0 5%;
  background: #222;
  font-family: "Roboto", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

::selection {
  background: cyan;
  color: #fff;
}

.Conteudo {
  display: flex;
  flex-direction: row;
  text-align: -webkit-center;
}

h1 {
  color:cyan;
}

@media (max-width: 1000px) {
  .Conteudo {
    flex-direction: column;
  }
}

.Pomodoro {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 530px;
  justify-content: space-between;
  align-items: center;
  padding: 50px 0;
}

.PainelTarefa {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;

  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}
