.TipoTempo {
    width: 400px;
    display: flex;
    margin-bottom: 5px;
}

@media screen and (max-width: 500px) {
    .TipoTempo {
        width: 100%;
    }
}

.TipoTempo button {
    flex: 1;
    border: 2px solid #333;
    border-right: 0;
    background: transparent;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    padding: 10px;
    color: #999;
    outline: 0;
    transition: all .3s;
    cursor: pointer;
    font-weight: 600;
}

.TipoTempo button:first-child {
    border-radius: 5px 0 0 5px;
}

.TipoTempo button:last-child {
    border-right: 2px solid #333;
    border-radius: 0 5px 5px 0;
}

.TipoTempo button.active {
    background: cyan;
    color: #333;
    box-shadow: 0 3px 10px rgba(0, 0, 0, .12);
}
