.Tarefas {
  width: 330px;
  height: 300px;
  overflow: hidden;
}

.Tarefas .Tarefas-box {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.07);
  transition: opacity 0.3s;
}

.Tarefa {
  width: 330px;
  background: #333;
  display: flex;
  padding: 15px;
  justify-content: space-between;
  align-items: center;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  color: #fff;
  cursor: grab;
  border-radius: 5px;
  margin-bottom: 5px;
}

.Tarefa input {
  width: 100%;
  font-family: "Roboto", sans-serif;
  font-size: 13px;
  border: none;
  outline: none;
  background: #333;
  color: #fff;
}
.Tarefa span {
  display: inline-block;
  font-family: monospace;
  background-color: #666;
  border: 1px solid #333;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1), inset 0 0 0 1px #666;
  color: cyan;
  font-size: 14px;
  margin: 0 0.15em;
  padding: 0.25em 0.7em;
  white-space: nowrap;
  width: 150px;
  text-align: center;
  cursor: pointer;
}
